<template>
  <div class="parent-login">
    <div class="page-content">
      <div class="holder">
        <div class="container">
          <div class="row justify-content-center">
            <div class="col-md-18 col-lg-12">
              <h2 class="text-center">تسجيل الدخول إلى حساب</h2>
              <div class="form-wrapper">
                <form action="#" @submit.prevent="validateLogin">
                  <div class="form-group">
                    <input type="text" class="form-control" placeholder="البريد الإلكتروني" v-model="emailValue" />
                  </div>
                  <Transition name="bounce">
                    <div class="alert alert-error" role="alert" v-show="errorValidate.errorEmail">{{ errorValidate.errorEmail }}</div>
                  </Transition>
                  <div class="form-group">
                    <input type="password" class="form-control" placeholder="كلمه السر" v-model="passwordValue" />
                  </div>
                  <Transition name="bounce">
                      <div class="alert alert-error" role="alert" v-if="errorValidate.errorPassword">
                        {{ errorValidate.errorPassword }}
                      </div>
                    </Transition>
                  <div class="text-center">
                    <button class="btn">تسجيل الدخول إلى حساب</button>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Login",
  data() {
    return {
      emailValue: "",
      passwordValue: null,
      errorValidate: {},
    };
  },
  methods: {
    validateLogin() {
      this.errorValidate = {};
      let emailUser = this.emailValue,
        regEmail = /\w+@\w+[.]\w+/gi,
        passwordUser = String(this.passwordValue),
        regPassword = /\d{6,}/gi;
      if (!regEmail.test(emailUser)) {
        this.errorValidate.errorEmail = "ادخل بريد الكتروني صحيح";
      }
      if (!regPassword.test(passwordUser)) {
        this.errorValidate.errorPassword = "يجب ان لا تكون كلمة السر اصغر من 6 ارقام علا الاقل";
      }
    },
  },
};
</script>

