<template>
  <div class="parent-register">
    <div class="page-content">
      <div class="holder">
        <div class="container">
          <div class="row justify-content-center">
            <div class="col-md-18 col-lg-12">
              <h2 class="text-center">تسجيل حساب</h2>
              <div class="form-wrapper">
                <p>للوصول إلى قائمة الرغبات ودفتر العناوين وتفضيلات الاتصال الخاصة بك وللاستفادة من الخروج السريع ، قم بإنشاء حساب معنا الآن.</p>
                <form action="#" @submit.prevent="validateRegister">
                  <div>
                    <div class="form-group">
                      <input type="text" class="form-control" placeholder="الاسم كامل" v-model="nameValue" required />
                    </div>
                    <div class="form-group">
                      <input type="number" class="form-control" placeholder="رقم الهاتف" v-model="phoneValue" required />
                    </div>
                    <Transition name="bounce">
                      <div class="alert alert-error" role="alert" v-if="errorValidate.errorPhone">
                        {{ errorValidate.errorPhone }}
                      </div>
                    </Transition>
                    <div class="form-group">
                      <input type="email" class="form-control" placeholder="البريد الإلكتروني" v-model="emailValue" required />
                    </div>
                    <Transition name="bounce">
                      <div class="alert alert-error" role="alert" v-show="errorValidate.errorEmail">{{ errorValidate.errorEmail }}</div>
                    </Transition>
                    <div class="form-group">
                      <input type="password" class="form-control" placeholder="كلمه السر" v-model="passwordValue" required />
                    </div>
                    <Transition name="bounce">
                      <div class="alert alert-error" role="alert" v-if="errorValidate.errorPassword">
                        {{ errorValidate.errorPassword }}
                      </div>
                    </Transition>
                    <div class="form-group">
                      <input type="password" class="form-control" placeholder="تأكيد كلمة السر" v-model="conPasswordValue" required />
                    </div>
                    <Transition name="bounce">
                      <div class="alert alert-error" role="alert" v-if="errorValidate.errorPassConPass">{{ errorValidate.errorPassConPass }}</div>
                    </Transition>
                    <div class="form-group select-wrapper">
                      <select class="form-control" v-model="selectValue">
                        <option :value="option.id" v-for="option in optionsArray" :key="option.id">{{ option.name }}</option>
                      </select>
                    </div>
                    <!-- <Transition name="bounce">
                      <div class="alert alert-error" role="alert" v-if="errorValidate.errorSelectValue">من فضلك حدد عنصر واحد علا الاقل</div>
                    </Transition> -->
                    <div class="text-center">
                      <button class="btn">انشئ حساب</button>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Register",
  data() {
    return {
      nameValue: "",
      phoneValue: null,
      emailValue: "",
      passwordValue: null,
      conPasswordValue: null,
      selectValue: "",
      errorValidate: {},
      optionsArray: [
        { id: 1, name: "القدرة المعرفية" },
        { id: 2, name: "القدرة المعرفية" },
        { id: 3, name: "القدرة المعرفية" },
        { id: 4, name: "القدرة المعرفية" },
      ],
    };
  },
  methods: {
    validateRegister() {
      if (this.nameValue || this.phoneValue || this.emailValue || this.passwordValue || this.conPasswordValue || this.selectValue) {
        this.errorValidate = {};
        let phoneUser = String(this.phoneValue),
          regPhone = /^[05]\d{8}/gi,
          emailUser = this.emailValue,
          regEmail = /\w+@\w+[.]\w+/gi,
          passwordUser = String(this.passwordValue),
          regPassword = /\d{6,}/gi,
          conPasswordUser = String(this.conPasswordValue);
        if (!regPhone.test(phoneUser)) {
          this.errorValidate.errorPhone = "من فضلك يجب ان يبدا رقم الهاتف ب 05 ويتكون من 8 ارقام";
        }
        if (!regEmail.test(emailUser)) {
          this.errorValidate.errorEmail = "ادخل بريد الكتروني صحيح";
        }
        if (!regPassword.test(passwordUser)) {
          this.errorValidate.errorPassword = "يجب ان لا تكون كلمة السر اصغر من 6 ارقام علا الاقل";
        }
        passwordUser === conPasswordUser ? "" : (this.errorValidate.errorPassConPass = "تاكيد كلمة السر غير متابقة لكلمة السر");
        // if (!this.selectValue) {
        //   this.errorValidate.errorSelectValue = "errorSelectValue";
        // }
        if (this.errorValidate.length > 0) {
          return false;
        } else {
          return true;
        }
      } else {
        console.log("Not Valid");
      }
    },
  },
};
</script>

<style lang="scss"></style>
