<script>
import json from "/src/assets/dataProduct.json";
import axios from "axios";
import Footer from "@/components/Footer.vue";
import { mapMutations, mapState } from "vuex";

// slider

export default {
  name: "BookViews",
  props: ["id"],
  components: {
    Footer,
  },

  data() {
    return {
      isLoading: false,
      quantity: 1,
    };
  },
  computed: {
    ...mapState(["dataBook"]),
  },
  mounted() {
    this.getBook(this.id);
    setTimeout(() => {
      this.slickSlider();
      this.isLoading = true;
    }, 500);
  },

  methods: {
    ...mapMutations(["getBook", "setCartIdToLS"]),
    slickSlider() {
      $(document).ready(function () {
        $(".slider-for").slick({
          slidesToShow: 1,
          slidesToScroll: 1,
          arrows: false,
          fade: true,
          asNavFor: ".slider-nav",
          rtl: true,
        });
        $(".slider-nav").slick({
          slidesToShow: 3,
          slidesToScroll: 1,
          asNavFor: ".slider-for",
          dots: true,
          centerMode: true,
          focusOnSelect: true,
          rtl: true,
        });
      });
    },
    toggleQnt(type) {
      if (type == "decrease") {
        this.quantity--;
      } else {
        this.quantity++;
      }
    },
  },
};
</script>

