import { createStore } from "vuex";
import axios from "axios";
export default createStore({
  state: {
    urlApi: "https://backend.qiyas.co/api",
    dataBooks: [],
    dataCategories: [],
    dataBook: [],
    imageSliders: [],
  },
  getters: {},
  mutations: {
    async getBooks(state) {
      await axios.get(`${state.urlApi}/books`).then((res) => {
        state.dataBooks = res.data.books;
      });
    },
    async getCategories(state) {
      await axios.get(`${state.urlApi}/books/category/2`).then((res) => {
        state.dataCategories = res.data.books;
      });
    },
    async getBook(state, payload) {
      await axios.get(`${state.urlApi}/book/${payload}`).then((res) => {
        state.dataBook = res.data.books;
      });
    },
    async getImgSliders(state) {
      await axios.get(`${state.urlApi}/sliders`).then((res) => {
        state.imageSliders = res.data.data;
      });
    },
    setCartIdToLS(state, payload) {
      let cartsID = JSON.parse(localStorage.getItem("cartId"));
      if (cartsID) {
        cartsID.push({ id: payload });
      } else {
        cartsID = [{ id: payload }];
      }
      localStorage.setItem("cartId", JSON.stringify(cartsID));
    },
  },
  actions: {},
  modules: {},
});
